e.commit(\"displayResultPopup\");\n        }\n      }\n  },\n\n  computed: {\n    launchButtonLabel() {\n      return this.$store.state.firstLaunch ? \"Commencer\" : \"Recommencer\"\n    },\n    score () {\n      return this.$store.state.score.total\n    },\n\n    highScore () {\n      return this.$store.state.score.high\n    },\n\n    diffScore () {\n      const state = this.$store.state;\n      if (!this.highScore || typeof this.score === \"undefined\" || !state.game.launched){\n        return \"\"\n      }\n      const currHigh = (state.score.high / state.game.nRuns) * state.game.currentRun;\n      return Math.round(this.score - currHigh);\n    },\n\n    launched () {\n      return this.$store.state.game.launched;\n    },\n\n    playerName () {\n      return this.$store.getters.playerName\n    },\n\n    unreadMessages () {\n      return this.$store.state.chat.unread;\n    },\n    canViewResults() {\n      return this.$store.state.game.status === constants.status.FINISHED\n    },\n\n    ...mapState([\"leaderboard\"])\n  }\n}\n</script>\n<style scoped>\n.nav {\n  text-align: left;\n  margin-bottom: 15px;\n  margin-top: -10px;\n  color: blue;\n}\n.nav a, .nav a:visited, .nav a:active, .nav a:link {\n  color: blue;\n}\n\n\n.controls {\n  position: relative;\n}\n\n.controls button {\n  margin-right: 5px;\n}\n\n#chat-toggle-button {\n  position: absolute;\n  top: 15px;\n  right: 19px;\n  color: blue;\n  cursor:pointer;\n}\n#chat-toggle-button.unread-message:after {\n  display: block;\n  width: 6px;\n  height:6px;\n  border-radius: 3px;\n  background: red;\n  content: \" \";\n  position: absolute;\n  top: 0;\n  right: 0;\n}\n#chat-toggle-button:hover {\n  color: #000085;\n}\n\n@media screen and (max-width: 600px) {\n  .nav {\n    display: none;\n  }\n\n  #launch:not(:disabled) {\n    z-index: 1001;\n  }\n\n  .controls button:disabled {\n    display: none;\n  }\n\n  .controls button:not(:disabled) {\n    position: fixed;\n    bottom: 20px;\n    left:50%;\n    transform: translate(-50%, 0);\n    background-color: white;\n    padding: 10px 20px;\n    font-size: 1.2em;\n    border-color: red;\n    color: red;\n    border-width: 2px;\n  }\n\n\n  #main-info-box {\n    text-align: right;\n    background-color: transparent;\n    color: red;\n    border:none;\n    margin: 0;\n    padding-bottom: 0;\n  }\n\n  #main-info-box.toggled .self {\n    display: none;\n  }\n\n  #main-info-box:not(.toggled) #player-list {\n    display: none;\n  }\n}\n</style>"],"sourceRoot":""}]);
