function generateFakeSummary(places, players=6){
  console.warn("'generateFakeSummary' should not be used in prod!!!")
    const records = [];
    places.forEach(place => {
      const recs = [];
      for (let i=0; i<players; i++){
        recs.push({
          guess: geoJitter(place),
          player: `Player_${i}`,
        });
      }
      records.push(recs);
    });
  return {places: places, records: records};
}

function generateFakeRunSummary(place, players=6){
  const recs = [];
  for (let i=0; i<players; i++){
    recs.push({
      guess: geoJitter(place, {lonFactor: 2, latFactor: 1}),
      player: `Player_${i}`,
    });
  }
  return recs;
}

