___CSS_LOADER_EXPORT___.push([module.id, "\n.sharer[data-v-037ecc41] {\n  cursor: pointer;\n  display: inline-block;\n  margin-right: 10px;\n}\na.sharing-link[data-v-037ecc41] {\n  color: white !important;\n  font-size: 1.1em;\n  font-weight: bold;\n  display: inline-block;\n  white-space: nowrap;\n  vertical-align: bottom;\n}\na.sharing-link[data-v-037ecc41]:hover {\n  color: white !important;\n}\na.sharing-link[data-v-037ecc41]:not(.link-collapsed):after {\n  display: block;\n  content: \"Lien copié !\";\n  background-color: black;\n  padding: 5px 10px;\n  position: absolute;\n  top: -10px;\n  left: 50%;\n  opacity: 0;\n  font-size: 0.8em;\n  transition: all 0.2s ease-in;\n}\na.sharing-link:not(.link-collapsed).copied[data-v-037ecc41]:after {\n  top: -25px;\n  opacity: 1;\n  transition: all 0.2s ease-out;\n}\n", "",{"version":3,"sources":["webpack://./front/ui/gameFooter/gameSharingLink.vue"],"names":[],"mappings":";AAmGA;EACE,eAAe;EACf,qBAAqB;EACrB,kBAAkB;AACpB;AACA;EACE,uBAAuB;EACvB,gBAAgB;EAChB,iBAAiB;EACjB,qBAAqB;EACrB,mBAAmB;EACnB,sBAAsB;AACxB;AACA;EACE,uBAAuB;AACzB;AAGA;EACE,cAAc;EACd,uBAAuB;EACvB,uBAAuB;EACvB,iBAAiB;EACjB,kBAAkB;EAClB,UAAU;EACV,SAAS;EACT,UAAU;EACV,gBAAgB;EAChB,4BAA4B;AAC9B;AAEA;EACE,UAAU;EACV,UAAU;EACV,6BAA6B;AAC/B","sourcesContent":["<template>\n  <footer-collapsible-section :collapsed=\"collapsed\">\n    <template v-slot:control>\n      <span class=\"sharer\" @click=\"toggleView\">\n      {{ message }}\n    </span>\n    </template>\n    <template v-slot:content>\n      <a ref=\"link\"\n         :class=\"{copied: copied, 'link-collapsed': collapsed}\"\n         class=\"sharing-link\"\n         :href=\"trueUrl\"\n         @click.prevent=\"copyUrlToClipboard\">{{ trueUrl }}</a>\n    </template>\n  </footer-collapsible-section>\n</template>\n\n<script>\nimport footerCollapsibleSection from \"./footerCollapsibleSection.vue\";\nexport default {\n  components: {\n    \"footer-collapsible-section\": footerCollapsibleSection,\n  },\n  data() {\n    const trueUrl = window.location.href\n    const url = trueUrl.replace(\"https://\", \"\")\n          .replace(\"www.\", \"\")\n          .replace(window.location.hash, \"\");\n    return {\n      url: url,\n      trueUrl: trueUrl,\n      collapsed: true,\n      timeout: undefined,\n      copied: false,\n    }\n  },\n  methods: {\n    copyUrlToClipboard() {\n      const selection = window.getSelection();\n      const range = document.createRange();\n      range.selectNodeContents(this.$refs.link);\n      selection.removeAllRanges();\n      selection.addRange(range);\n      // this.$refs.link.select();\n      document.execCommand(\"copy\");\n      this.copied = true;\n      const duration = 0.6\n      window.setTimeout(() => {\n        this.copied = false;\n      }, duration*1000);\n      this.timeout = this.startTimeout(() => {\n        this.hideLink();\n      }, (duration + 0.3) *1000);\n    },\n\n    clearTimeout() {\n      if (typeof this.timeout !== \"undefined\") {\n        window.clearTimeout(this.timeout);\n        this.timeout = \"undefined\";\n      }\n    },\n\n    startTimeout(handler, duration) {\n      this.clearTimeout();\n      this.timeout = window.setTimeout(handler, duration);\n    },\n\n    showLink() {\n      this.clearTimeout();\n      this.collapsed = false;\n    },\n\n    hideLink() {\n      this.collapsed = true;\n      this.copied = false;\n      this.clearTimeout();\n    },\n\n    toggleView() {\n      if (this.collapsed){\n        this.showLink();\n        this.timeout = this.startTimeout(() => {\n          this.hideLink();\n        }, 20*1000);\n      } else {\n        this.hideLink();\n      }\n    },\n  },\n\n  computed: {\n    message() {\n      return this.collapsed ? \"Inviter des amis\" : \"Lien à partager : \";\n    }\n  }\n}\n</script>\n\n<style scoped>\n.sharer {\n  cursor: pointer;\n  display: inline-block;\n  margin-right: 10px;\n}\na.sharing-link {\n  color: white !important;\n  font-size: 1.1em;\n  font-weight: bold;\n  display: inline-block;\n  white-space: nowrap;\n  vertical-align: bottom;\n}\na.sharing-link:hover {\n  color: white !important;\n}\n\n\na.sharing-link:not(.link-collapsed):after {\n  display: block;\n  content: \"Lien copié !\";\n  background-color: black;\n  padding: 5px 10px;\n  position: absolute;\n  top: -10px;\n  left: 50%;\n  opacity: 0;\n  font-size: 0.8em;\n  transition: all 0.2s ease-in;\n}\n\na.sharing-link:not(.link-collapsed).copied:after {\n  top: -25px;\n  opacity: 1;\n  transition: all 0.2s ease-out;\n}\n</style>"],"sourceRoot":""}]);
