turn {\n        autozoomHandler: new BooleanSettingHandler(\"autozoom\", this.$store),\n        url: \"\"\n      }\n    },\n\n    computed: {\n      currentGame() {\n        return this.$store.state.game.displayName;\n      },\n      currentRun() {\n        return this.$store.state.game.currentRun;\n      },\n      totalRuns() {\n        return this.$store.state.game.nRuns;\n      },\n      canEdit() {\n        return this.$store.state.game.status === constants.status.FINISHED\n      }\n    },\n\n    created() {\n      this.url = window.location.href\n          .replace(\"https://\", \"\")\n          .replace(\"www.\", \"\")\n          .replace(window.location.hash, \"\");\n    },\n\n    methods: {\n      toggleAutozoom() {\n        let value = this.autozoomHandler.read();\n        this.autozoomHandler.write(!value);\n      },\n\n      openChangePopup() {\n        if (this.canEdit){\n            this.$store.commit(\"showGameCreator\");\n            this.$store.commit(\"displayResultPopup\");\n        }\n      }\n    }\n  }\n\n</script>\n\n<style scoped>\n.current-game {\n  margin-right: 10px;\n}\n\n.change-game {\n  margin-left: 5px;\n  color: white !important;\n  text-decoration: underline;\n  font-size: 0.9em;\n  cursor:pointer;\n}\n\n.current-game .fas {\n  margin-right: 8px;\n}\n  .footer {\n    position: fixed;\n    bottom: 0;\n    left: 0;\n    background: blue;\n    color: white;\n    z-index: 1000;\n    padding: 2px 10px;\n    font-size: 0.9em;\n    display: flex;\n  flex-flow: row nowrap;\n  justify-content: flex-start;\n  align-items: center;\n  }\n\n  .footer .sharing-link {\n    color: white;\n    margin-right: 15px;\n    font-size: 1.1em;\n    font-weight: bold;\n  }\n\n\n</style>"],"sourceRoot":""}]);
