___CSS_LOADER_EXPORT___.push([module.id, "\n\n/** POPUP LAYOUT **/\n.popup-left[data-v-0b2bc3c9] {\n  position: fixed;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  min-height: 0;\n  width: 180px;\n  border-right: 1px solid lightgray;\n  margin-bottom: 0;\n  overflow-x: hidden;\n  overflow-y: auto;\n\n  resize: horizontal;\n  background-color: white;\n  transition: width 0.2s ease;\n}\n.popup-left[data-v-0b2bc3c9]:hover, .popup-left[data-v-0b2bc3c9]:focus {\n  width: 220px;\n}\n.popup-main[data-v-0b2bc3c9] {\n  margin-left: 200px;\n}\n\n/** ELEMENTS **/\n.close[data-v-0b2bc3c9] {\n    position: absolute;\n    top: 7px;\n    right: 7px;\n    color: #a1a1a1;\n    cursor:pointer;\n  z-index: 1;\n}\n.close[data-v-0b2bc3c9]:hover {\n  color: blue;\n}\n.high-score-notif[data-v-0b2bc3c9] {\n  margin-top: 0;\n  font-style: italic;\n  color: gray;\n}\n.popup-buttons[data-v-0b2bc3c9] {\n  margin: 0 auto;\n  margin-top: 15px;\n}\n.popup-buttons button[data-v-0b2bc3c9]{\n  margin-right: 10px;\n}\n.creator-wrapper button[data-v-0b2bc3c9] {\n  margin-left: 10px;\n}\n@media screen and (max-width: 600px) {\n#popup .popup-main[data-v-0b2bc3c9] {\n      display: flex;\n      flex-flow: column nowrap;\n      justify-content: center;\n      margin: 0;\n}\n#popup.popup-top[data-v-0b2bc3c9] {\n      justify-content: start;\n}\n}\n", "",{"version":3,"sources":["webpack://./front/panels/resultPopup/resultPopup.vue"],"names":[],"mappings":";;AA0IA,mBAAmB;AAEnB;EACE,eAAe;EACf,MAAM;EACN,OAAO;EACP,SAAS;EACT,aAAa;EACb,YAAY;EACZ,iCAAiC;EACjC,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;;EAEhB,kBAAkB;EAClB,uBAAuB;EACvB,2BAA2B;AAC7B;AAEA;EACE,YAAY;AACd;AAGA;EACE,kBAAkB;AACpB;;AAEA,eAAe;AAEf;IACI,kBAAkB;IAClB,QAAQ;IACR,UAAU;IACV,cAAc;IACd,cAAc;EAChB,UAAU;AAEZ;AACA;EACE,WAAW;AACb;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,WAAW;AACb;AAEA;EACE,cAAc;EACd,gBAAgB;AAClB;AAEA;EACE,kBAAkB;AACpB;AAEA;EACE,iBAAiB;AACnB;AAEA;AACE;MACI,aAAa;MACb,wBAAwB;MACxB,uBAAuB;MACvB,SAAS;AACb;AAEA;MACI,sBAAsB;AAC1B;AACF","sourcesContent":["<template>\n  <popup :visible=\"visible\">\n    <i class=\"close fas fa-times\" @click=\"close\"></i>\n    <div class=\"popup-left\" v-if=\"!isMobile\">\n      <div class=\"chat\">\n        <chat-inner></chat-inner>\n      </div>\n    </div>\n    <div class=\"popup-main\">\n      <template v-if=\"gameCreatorMode\">\n        <h2 class=\"blue\">\n          Changer de partie\n        </h2>\n        <div class=\"creator-wrapper\">\n          <game-creator\n            :datasets=\"datasets\"\n            :action=\"{'method': 'emit'}\"\n            @launch=\"createNewGame\"\n          >\n            <button class=\"cancel-button\" @click=\"closeCreator\">Annuler</button>\n          </game-creator>\n        </div>\n      </template>\n      <template v-else>\n        <h2 class=\"blue\">\n          {{ title }}\n        </h2>\n        <h4 class=\"high-score-notif\" id=\"new-highscore-notif\" v-if=\"newHighScore\">\n          C'est votre meilleur score !\n        </h4>\n        <result-table :leaderboard=\"leaderboard\"></result-table>\n        <div class=\"popup-buttons\">\n          <button @click=\"close\">Voir la correction</button>\n          <button id=\"relaunch-from-popup\" @click=\"relaunch\" class=\"main-button\">Nouvelle partie</button>\n          <button @click=\"openCreator\">Changer de carte</button>\n        </div>\n        <div style=\"margin-top:15px;\">\n          <a  href=\"/\" class=\"low-key\">\n            Retour à l'accueil\n          </a>\n        </div>\n      </template>\n    </div>\n  </popup>\n</template>\n\n<script>\nimport Popup from \"../../components/popup.vue\";\nimport resultTable from \"./resultTable.vue\";\nimport {mapState, mapGetters} from \"vuex\";\nimport constants from \"../../common/constants\";\nimport {GET, goToHash} from \"../../common/utils\";\nimport chatInner from \"../../components/chatInner.vue\";\nimport GameCreator from \"../../lobby/gameCreator.vue\";\n\nexport default {\n  components: {\n    GameCreator,\n    \"popup\": Popup,\n    \"result-table\": resultTable,\n    \"chat-inner\": chatInner,\n  },\n  data() {\n    return {\n      datasets: [\n        {map_id: undefined, name: \"Chargement...\"}\n      ],\n    }\n  },\n  computed: {\n    latestScore: function() {\n      const res = this.results.find(rec => (rec.player === this.$store.state.playerId));\n      if (res) {\n        return res.score;\n      }\n      return undefined;\n    },\n\n    title: function (){\n      return this.status === constants.status.FINISHED ? \"Partie terminée\" : \"Partie en cours...\"\n    },\n\n    ...mapState({\n      visible: state => state.ui.resultPopup,\n      gameCreatorMode: state => state.ui.showGameCreator,\n      results: state => state.game.results,\n      highScore: state => state.score.high,\n      leaderboard: state => state.leaderboard,\n      status: state => state.game.status,\n    }),\n\n    ...mapGetters([\n      \"newHighScore\",\n      \"finalScore\",\n    ]),\n  },\n  mounted() {\n    GET(\"/datasets\").then(datasets => {\n      this.datasets = datasets;\n    });\n  },\n  methods: {\n    close: function(){\n      //this.$store.commit(\"hideResultPopup\");\n      goToHash(\" \");\n    },\n\n    relaunch: function() {\n      this.$socketEmit(\"launch\");\n    },\n\n    openCreator: function() {\n      this.$store.commit(\"showGameCreator\");\n    },\n\n    closeCreator: function() {\n      this.$store.commit(\"hideGameCreator\");\n    },\n\n    createNewGame: function (data) {\n      console.log(\"Requesting game change\", data);\n      this.$socketEmit(\"request-game-change\", data);\n    },\n  },\n\n  watch: {\n    newHighScore(value) {\n      if (value) {\n        console.log(\"New high score\", this.finalScore, \"!\");\n        this.$cookie.highScore.write(this.finalScore);\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n\n/** POPUP LAYOUT **/\n\n.popup-left {\n  position: fixed;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  min-height: 0;\n  width: 180px;\n  border-right: 1px solid lightgray;\n  margin-bottom: 0;\n  overflow-x: hidden;\n  overflow-y: auto;\n\n  resize: horizontal;\n  background-color: white;\n  transition: width 0.2s ease;\n}\n\n.popup-left:hover, .popup-left:focus {\n  width: 220px;\n}\n\n\n.popup-main {\n  margin-left: 200px;\n}\n\n/** ELEMENTS **/\n\n.close {\n    position: absolute;\n    top: 7px;\n    right: 7px;\n    color: #a1a1a1;\n    cursor:pointer;\n  z-index: 1;\n\n}\n.close:hover {\n  color: blue;\n}\n\n.high-score-notif {\n  margin-top: 0;\n  font-style: italic;\n  color: gray;\n}\n\n.popup-buttons {\n  margin: 0 auto;\n  margin-top: 15px;\n}\n\n.popup-buttons button{\n  margin-right: 10px;\n}\n\n.creator-wrapper button {\n  margin-left: 10px;\n}\n\n@media screen and (max-width: 600px) {\n  #popup .popup-main {\n      display: flex;\n      flex-flow: column nowrap;\n      justify-content: center;\n      margin: 0;\n  }\n\n  #popup.popup-top {\n      justify-content: start;\n  }\n}\n</style>"],"sourceRoot":""}]);
